import React, { useState, useEffect } from "react";
import { ethers } from "ethers";
import Plot from "./components/plot";
import Menu from "./components/Menu/menu";
import { ThemeProvider } from "@mui/material";
import { defaultTheme } from "./layout/theme";
import Button from "./components/Button/button";
// if you delete this svg, be careful ab postbuild script
import logo from "./logo.svg";
import "./index.css";

function App() {
  const [provider, setProvider] = useState({});
  const [signer, setSigner] = useState({});
  const [cropInt, setCropInt] = useState(null);
  const [userAccount, setUserAccount] = useState("");
  const [isWalletConnected, setWalletConnected] = useState(false);

  async function updateEthers() {
    // A Web3Provider wraps a standard Web3 provider, which is
    // what MetaMask injects as window.ethereum into each page
    const tempProvider = new ethers.providers.Web3Provider(window.ethereum);

    // MetaMask requires requesting permission to connect users accounts
    await tempProvider.send("eth_requestAccounts", []);

    // The MetaMask plugin also allows signing transactions to
    // send ether and pay to change state within the blockchain.
    // For this, you need the account signer...
    const tempSigner = tempProvider.getSigner();

    setProvider(tempProvider);
    setSigner(tempSigner);
  }

  async function connectWallet() {
    // ❌ Check if Meta Mask Extension exists
    if (window.ethereum) {
      try {
        const accounts = await window.ethereum.request({
          method: "eth_requestAccounts",
        });
        setUserAccount(accounts[0]);
        setWalletConnected(true);
      } catch (error) {
        console.log("Error connecting...");
      }
    } else {
      alert("Meta Mask not detected");
    }

    updateEthers();
  }
  return (
    <div className="fixed bottom-0 right-0 z-[9999]">
      <ThemeProvider theme={defaultTheme}>
        <header className="App-header"></header>
        <main>
          {/* <div className="farm"> */}
          <div className="flex flex-row pl-[24px] absolute right-0 bottom-0 w-[312px] h-[96px] bg-cover bg-[url('https://blockcrops.s3.amazonaws.com/images/bg.png')]">
            <Plot
              cropInt={cropInt}
              plotNum={0}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
            <Plot
              cropInt={cropInt}
              plotNum={1}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
            <Plot
              cropInt={cropInt}
              plotNum={2}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
            <Plot
              cropInt={cropInt}
              plotNum={3}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
            <Plot
              cropInt={cropInt}
              plotNum={4}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
            <Plot
              cropInt={cropInt}
              plotNum={5}
              userAccount={userAccount}
              provider={provider}
              signer={signer}
            />
          </div>
        </main>
        {isWalletConnected ? (
          <Menu
            setCropInt={setCropInt}
            userAccount={userAccount}
            provider={provider}
            signer={signer}
          />
        ) : (
          <Button variant="contained" label="Connect" onClick={connectWallet} />
        )}
      </ThemeProvider>
    </div>
  );
}

export default App;
